html, body {
  height: 100%;
}

#root {
  min-height: 100%;
  height: 1px;
}

.jumbotron {
  color: #fff;
  background-color: transparent;
  text-shadow: .15rem .15rem .75rem #000;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
